export default {
  props:['level'],
  data(){
    return{
  
    }
  },
  mounted(){

  },
  computed:{
    allStar(){
        return Math.floor(this.level)
    },
    halfStar(){
        return (this.level+'').indexOf('.')==-1?false:1;
    },
    balckStar(){
        return this.level<5?Math.floor(5-this.level):0;
    }
  }
    

}    



